<template>
<div id="record-product">
<van-nav-bar
  left-arrow
  class="record-product-nav"
   @click-left="leftClick"
>
<template #title>
	<font color="white">记录操作</font>
</template>

</van-nav-bar>
	<car-content/>
		<div class="bottom-box">
		<i class="btn-icon" @click="sendClick">生成订单(图片形式)</i>
	</div>
</div>

</template>

<script>
import {NavBar} from 'vant'
import CarContent from './childComps/CarContent'
import {addCar} from 'network/car'


export default {
  name: 'Car',
	components:{
		[NavBar.name]:NavBar,
		CarContent
	},
	data(){
		return{
			show:false,
			recordObj:{
				name:'',
				status:'0',
			},
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},
		//确定-发送
		sureClick(){

		},
		sendClick(){
			if(this.$store.getters.getItem.length===0){this.$toast("请添加产品"); return false}
			//发送到后台
			//console.log(this.$store.getters.getItem)
			//后台生成图片 把地址保存到数据库 再返回出来
			//没有选中不能生成-(后台需要一个对象不是对象就报错)
			let obj=this.$store.getters.getItem.length==0?{}:this.$store.getters.getItem
			//如果没有对象就不发送到后台
			let sendObj={
				list:obj
			}
			let _self=this
			addCar(sendObj).then(res=>{
				_self.$toast.loading({
					message: '生成中...',
					forbidClick: true,
					onClose:function(){
						//三秒后跳转
						if(res['flag']){
							_self.$toast("生成成功")
							//跳转到详细页面
							_self.$router.push({name:'carlist',params:{obj:res}}).catch(err=>err)
						}else{
							_self.$toast("该图片已经存在")
							return false
						}
					}
				})
			})
		}
	}
}
</script>

